.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    opacity: 1;
    visibility: visible;
    z-index: 1000;
  
    width: 100%;
    height: 100%;
    padding: 20px;
  
    background-color: var(--backdrop-background);
  
    transition: opacity var(--animation),
      visibility var(--animation);

      &.is-hidden{
        opacity: 0;
        pointer-events: none;
        visibility: hidden;
      }
  }
  
