:root {
  --page-font: "Roboto", sans-serif;
  --page-font-weight: 500;
  --animation: 250ms cubic-bezier(.4, 0, .2, 1);
  --animation-icons: 500ms ease-in-out;
  --dark-theme-background-color: #202021;
  --second-animation: 250ms linear;
  --page-font: "Roboto", sans-serif;
  --page-font-weight: 500;
  --backdrop-background: rgba(0, 0, 0, .2);
  --animation: 250ms cubic-bezier(.4, 0, .2, 1);
  --animation-icons: 500ms ease-in-out;
  --main-text-color: #000;
  --accent-btn-color: #ff6b01;
  --second-text-color: #fff;
  --accent-color: #ff6b08;
  --footer-background: #f7f7f7;
  --light-theme-background-color: #fff;
  --dark-theme-modal-card: #484848;
  --second-animation: 250ms linear;
  --additional-accent-color: #ff001b;
  --modal-font-color: #8c8c8c;
  --border-footer-color: #545454;
  --second-icon-fill: #fbb168;
  --dark-background-trailer: #010001;
  --backdrop-background-trailer: #0f0f0f;
  --fill-hover-trailer-close-btn: #900808;
}

span, p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

ul {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

body {
  font-family: var(--page-font);
  font-weight: var(--page-font-weight);
  height: 100%;
  margin: 0;
  font-size: 12px;
  font-style: normal;
  line-height: 1.3;
  transition: background-color .25s linear;
}

@media screen and (min-width: 1280px) {
  body {
    font-size: 20px;
    line-height: 1.2;
  }
}

body.modal-open {
  overflow: hidden;
}

body.light-theme {
  background-color: var(--light-theme-background-color);
}

body.dark-theme {
  background-color: var(--dark-theme-background-color);
}

.container {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 320px) {
  .container {
    max-width: 320px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    max-width: 1280px;
  }
}

.wrapper {
  flex-wrap: wrap;
  display: flex;
}

.library__warning-sign {
  text-align: center;
  min-height: auto;
  display: block;
}

.movie-cards-gallery {
  min-height: 100vh;
  text-align: center;
  justify-content: center;
  margin: 0 auto;
  padding: 20px 20px 40px;
  list-style: none;
}

.movie-cards-gallery.dark-theme {
  color: var(--second-text-color);
}

@media screen and (min-width: 768px) {
  .movie-cards-gallery {
    max-width: 768px;
    flex-wrap: wrap;
    gap: 32px;
    padding: 60px 32px;
    display: flex;
  }
}

@media screen and (min-width: 1280px) {
  .movie-cards-gallery {
    max-width: 1281px;
    gap: 32px 16px;
  }
}

.card-container {
  width: 280px;
  max-height: 443px;
  cursor: pointer;
  transition: scale var(--animation), box-shadow var(--animation);
  margin: 0 auto;
}

.card-container:not(:last-child) {
  margin-bottom: 20px;
}

@media screen and (min-width: 768px) {
  .card-container {
    width: 336px;
    max-height: 495px;
  }

  .card-container:not(:last-child) {
    margin-bottom: 0;
  }
}

@media screen and (min-width: 1280px) {
  .card-container {
    width: 394px;
    max-height: 634px;
  }
}

.card-container:hover, .card-container:focus {
  scale: 1.01;
  box-shadow: 23px -36px 55px -17px rgba(255, 107, 8, .76);
}

.image-poster {
  width: 280px;
  height: 402px;
  border-radius: 5px;
}

@media screen and (min-width: 768px) {
  .image-poster {
    width: 336px;
    height: 455px;
  }
}

@media screen and (min-width: 1280px) {
  .image-poster {
    width: 395px;
    height: 574px;
  }
}

.movie-data {
  margin-top: 10px;
  margin-bottom: 0;
  font-size: 12px;
  line-height: 14px;
}

@media screen and (min-width: 768px) {
  .movie-data {
    margin-top: 8px;
    line-height: 16px;
  }
}

@media screen and (min-width: 1280px) {
  .movie-data {
    margin-top: 12px;
    font-size: 20px;
    line-height: 24px;
  }
}

.genre-year {
  color: var(--accent-color);
  line-height: 16px;
}

@media screen and (min-width: 1280px) {
  .genre-year {
    line-height: 24px;
  }
}

.swiper-container {
  padding-top: 20px;
  padding-bottom: 0;
}

@media screen and (min-width: 768px) {
  .swiper-container {
    padding-top: 60px;
  }
}

.swiper-container.dark-theme {
  color: var(--second-text-color);
}

.swiper-title {
  text-transform: uppercase;
  text-align: center;
  margin-bottom: 10px;
}

.swiper {
  width: 100%;
  height: 180px;
}

.swiper-image {
  width: 100%;
  height: 100%;
  border-radius: 4%;
}

.swiper-rating {
  width: 40px;
  height: 15px;
  background-color: var(--accent-color);
  color: var(--second-text-color);
  border-radius: 5%;
  justify-content: center;
  align-items: center;
  font-size: 13px;
  line-height: 1.1;
  display: flex;
  position: absolute;
  bottom: 5px;
  right: 5px;
}

.swiper-slide {
  transition: scale var(--second-animation);
  position: relative;
}

.swiper-slide:hover, .swiper-slide:focus {
  cursor: pointer;
  scale: 1.04;
}

.swiper-button-next, .swiper-button-prev {
  color: var(--accent-btn-color);
  transition: background-color var(--second-animation);
  border-radius: 10%;
}

.swiper-button-next:hover, .swiper-button-next:focus, .swiper-button-prev:hover, .swiper-button-prev:focus {
  background-color: rgba(226, 230, 220, .733);
}

@keyframes spinner-line-fade-more {
  0%, 100% {
    opacity: 0;
  }

  1% {
    opacity: 1;
  }
}

@keyframes spinner-line-fade-quick {
  0%, 39%, 100% {
    opacity: .25;
  }

  40% {
    opacity: 1;
  }
}

@keyframes spinner-line-fade-default {
  0%, 100% {
    opacity: .22;
  }

  1% {
    opacity: 1;
  }
}

@keyframes spinner-line-shrink {
  0%, 25%, 100% {
    opacity: .25;
    transform: scale(.5);
  }

  26% {
    opacity: 1;
    transform: scale(1);
  }
}

.btn {
  cursor: pointer;
  border: 1px solid var(--main-text-color);
  width: 110px;
  height: 44px;
  text-transform: uppercase;
  transition: box-shadow, background-color var(--animation);
  background-color: rgba(0, 0, 0, 0);
  border-radius: 5px;
